.App {
  text-align: center;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.container-fluid {
  display: flex; /* or inline-flex */
 padding:15px;
 margin:0px;
 background-image:  url('./imgs/bg.jpg');
 background-repeat:no-repeat;
 background-size: cover;
 justify-content: center;
 flex-direction: row;
}
.row {
 display: flex;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.row1 {

  flex-wrap: wrap;
}
.col1 {
  width: 100%;;
  padding:0;  
}
.col-forms {
  padding:0;
  display: flex;
}

.logo {
  margin-bottom: 40px ;
}
.logo-inter {
  width: 200px;
  height: 200px;
  background-size: contain;
  background-image: url('./imgs/logo-final.png');
}
.inputs-form {
  background-color: rgba(0,69,75,0.8);
  margin-top:auto;
  margin-bottom: auto;
}
input {
  stop-opacity: 1;
  opacity: 100%;
  background-color: white;
}
.submit-btn {
  background-color: #b86539;
}
.input-div {
  margin-bottom: 5px;
}
.forms {
  opacity: initial;
  text-align: left;
  padding: 15px 25px;
}
label {
  color: white;
}
.title{ 
  font-family: "LemonMilkBold";
  color:white;
  text-align: center; 
  margin-bottom: 12px;
  padding: 15px;
  padding-bottom: 0px;
}
.subtitle {
  color:white;
  text-align: center; 
  font-size: 20px;
  margin-bottom: 12px;
  padding: 5px 8px;
  padding-top: 0;
}

.input {
  border-radius: 5px;
}

.btn1 {
  background-color: #bbc539;
  margin-top:10px;
  border:0px;
  border-radius: 5px;
  color:white;
  font-weight: bold;
  font-size: 20px;
  padding:10px;
  width: 200px;
}

.btn1:hover{
  background-color: #bcc410;

}

.line {
  background-color: white;
  height: 1px;
  margin:100px;
  margin-top: 0px;
  margin-bottom:20px;
}
@font-face {
  font-family: "LemonMilk";
  src: url("./imgs/LemonMilk.otf");
}
@font-face {
  font-family: "LemonMilkBold";
  src: url("./imgs/LemonMilkbold.otf");
}

.input1 {
  max-height: 38px;
  background-color: white;
  resize: none;
}